
import header from '../public/header'
import footer from '../public/footer'
import consult from '../public/consult'
import nav from '../public/nav'
import towBanner from '../../assets/towBanner.jpg'
import newsList1 from '../../assets/newsList1.jpg'
import newsList2 from '../../assets/newsList2.jpg'
import newsList3 from '../../assets/newsList3.jpg'
import newsList4 from '../../assets/newsList4.jpg'
import newsList5 from '../../assets/newsList5.jpg'
import newsList6 from '../../assets/newsList6.jpg'
import newsList7 from '../../assets/newsList7.jpg'
import newsList8 from '../../assets/newsList8.jpg'
import newsList9 from '../../assets/newsList9.jpg'
import newsList10 from '../../assets/newsList10.jpg'
import newsList11 from '../../assets/newsList11.jpg'

import Panel from 'vux/src/components/panel/index.vue'

export default {
	
  components: {
  	headerV : header,
  	footerV : footer,
  	consultV : consult,
  	navV : nav, Panel
  },
  data () {
    return {
    	banner : [
    		{src : towBanner,desc : ' '}
    	],
    	list_1 : true, list_2 : false, list_3 : false, type : '1',
    	list: [
    		{
		       src: newsList1,
		       title: '',
		       desc: "2016-12-29   : 10366",
		       url : '/newsMsg'
		    }, 
		    {
		       src: newsList2,
		       title: '',
		       desc: '2016-12-24    : 4530',
		       url : '/newsMsg'
		    },
		    {
		       src: newsList3,
		       title: '|',
		       desc: '2016-12-24    : 14426',
		       url : '/newsMsg'
		    },
		    {
		       src: newsList4,
		       title: '',
		       desc: '2016-12-24  : 2188',
		       url : '/newsMsg'
		    },
		    {
		       src: newsList5,
		       title: '',
		       desc: '2016-10-20  : 3691',
		       url : '/newsMsg'
		    }
      	],
      	list2: [
    		{ 
		        src: newsList6,
		        title: ' ',
		        desc: '2016-10-20   	  : 2755',
		       	url : '/newsMsg'
		    }, 
		    {
		        src: newsList7,
		        title: ' ',
		        desc: '2016-10-14   : 1405',
		      	url : '/newsMsg'
		    },
		    {
		        src: newsList8,
		        title: '3D',
		        desc: '2016-10-11   : 1050',
		       	url : '/newsMsg'
		    },
		    {
		        src: newsList9,
		        title: '',
		        desc: '2016-10-10  : 5638',
		      	url : '/newsMsg'
		    },
		    {
		        src: newsList10,
		        title: ' 3D\n',
		        desc: '2016-10-08  : 3410',
		       	url : '/newsMsg'
		    }
      	],
      	list3 : [
      		{
      			src: newsList11,
		     	title: '',
		       	desc: '2016-11-01    : 2401',	
		       	url : '/newsMsg'
      		},
      		{
      			src: newsList11,
		     	title: '',
		       	desc: '2016-11-01    : 2401',	
		       	url : '/newsMsg'
      		},
      		{
      			src: newsList11,
		     	title: '',
		       	desc: '2016-11-01    : 2401',	
		       	url : '/newsMsg'
      		},
      		{
      			src: newsList11,
		     	title: '',
		       	desc: '2016-11-01    : 2401',	
		       	url : '/newsMsg'
      		}
      	]
    }
  },
  methods : {
  	news (ev){
  		if(ev.target.innerHTML == ''){
  			this.list_1 = true; this.list_2 = false; this.list_3 = false;
  		}else if(ev.target.innerHTML == ''){
  			this.list_1 = false; this.list_2 = true; this.list_3 = false;
  		}else{
  			this.list_1 = false; this.list_2 = false; this.list_3 = true;
  		}
  	}
  },
  mounted () {
  	setTimeout(()=>{	
  		this.$refs.a[0].style.right="0"
  	},300)
  }
}




